.sidebar-section {
  display: flex;
  background: linear-gradient(135deg, #45113d, #0a2e2b, #2b273f);
  color: #e5e7eb;
}

/* ===== SIDEBAR ===== */
.sidebar {
  height: 100vh;
  width: 90px;
  position: fixed;
  background: linear-gradient(180deg, #020617, #0f172a);
  border-right: 1px solid rgba(0, 173, 181, 0.2);
  box-shadow: 4px 0 20px rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease;
  z-index: 1000;
}

.sidebar-toggle {
  width: 220px;
}


/* ===== TOGGLE ICON ===== */
.sidebar-toggle-icons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 10px;
  cursor: pointer;
}

.sidebar-toggle-icons p svg {
  background: linear-gradient(135deg, #00adb5, #0284c7);
  color: #020617;
  margin-top: 12px;
  padding: 6px;
  border-radius: 8px;
  box-shadow: 0 6px 15px rgba(0, 173, 181, 0.4);
  transition: all 0.3s ease;
}

.sidebar-toggle-icons p svg:hover {
  transform: rotate(180deg) scale(1.05);
}

/* ===== ICON HOVER EFFECT ===== */
.sidebar a {
  color: #cbd5f5;
  transition: all 0.3s ease;
}

.sidebar a:hover {
  background: rgba(0, 173, 181, 0.15);
  color: #00adb5;
  border-radius: 8px;
}

/* ==== MOBILE VIEW ========== */
@media (max-width: 600px) {
  .sidebar {
    display: none;
  }
}
