.about {
  margin: 30px 0px;
  padding: 20px;
  margin-left: 200px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

/* .about-img img {
  border-radius: 10px;
  width: 90%;
  height: 300px;
} */

.about-img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-img img {
  width: 90%;
  height: 340px;

  object-fit: cover;
  object-position: top center;

  border-radius: 14px;

  padding: 6px;
  background: linear-gradient(
    270deg,
    #06a8a0,
    #00c6ff,
    #f30790
  );
  background-size: 300% 300%;
  animation: borderMove 4s linear infinite;

  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

@keyframes borderMove {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}


.about-img img:hover {
  transform: scale(1.04);
  box-shadow: 0 12px 35px rgba(19, 135, 129, 0.45);
}




.about-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-content h1 {
  color: gray;
  font-family: cursive;
  border-bottom: 2px solid gray;
  text-align: center;
}
.about-content p {
  text-align: justify;
  padding: 20px;
}

/* ======== DARK MODE ========= */

#dark .about {
  box-shadow: none;
  background-color: rgba(0, 0, 0, 0.15);
}

#dark .about-content h1 {
  color: #b0a4a4;
  border-bottom: 2px solid #b0a4a4;
}

/* ================================ */
@media (max-width: 600px) {
  .about {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    width: 100%;
  }
  .about-content h1 {
    margin-top: 10px;
  }
}