/* ===== PROFILE IMAGE ===== */
.navbar-profile-pic img {
  border-radius: 50%;
  height: 150px;
  width: 150px;
  display: block;
  margin: 20px auto;
  border: 3px solid #00adb5;
  box-shadow: 0 10px 30px rgba(0, 173, 181, 0.35);
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

.navbar-profile-pic img:hover {
  transform: scale(1.05);
  box-shadow: 0 15px 40px rgba(0, 173, 181, 0.6);
}

/* ===== NAV ITEM WRAPPER ===== */
.nav-item {
  color: #e5e7eb;
  margin-top: 20px;
  margin-left: 20px;
}

/* ===== NAV LINK CONTAINER ===== */
.nav-link {
  cursor: pointer;
  margin-top: 5px;
  /* padding-bottom: 40px; */
}

/* ===== REACT-SCROLL LINK ===== */
.nav-link a {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  border-radius: 10px;
  color: #e5e7eb;
  text-decoration: none;
  transition: all 0.3s ease;
}

/* ICON */
.nav-link a svg {
  margin-right: 0;
  font-size: 18px;
  transition: transform 0.3s ease;
}

/* ===== HOVER EFFECT ===== */
.nav-link a:hover {
  background: rgba(0, 173, 181, 0.15);
  color: #00adb5;
  transform: translateX(6px);
}

.nav-link a:hover svg {
  transform: rotate(-6deg) scale(1.1);
}

/* ===== ACTIVE LINK ===== */
.nav-link a.active {
  background: linear-gradient(135deg, #00adb5, #0284c7);
  color: #020617;
  font-weight: 600;
  box-shadow: 0 6px 18px rgba(0, 173, 181, 0.45);
  position: relative;
}

/* LEFT INDICATOR BAR */
.nav-link a.active::before {
  content: "";
  position: absolute;
  left: -10px;
  height: 60%;
  width: 4px;
  background: #ffd369;
  border-radius: 0 4px 4px 0;
  animation: slideIn 0.4s ease;
}

/* ===== ANIMATIONS ===== */
@keyframes slideIn {
  from {
    height: 0;
    opacity: 0;
  }
  to {
    height: 60%;
    opacity: 1;
  }
}
