/* WRAPPER */
.education-wrapper {
  margin-top: 70px;
   margin-left: 190px;
}

/* HEADING */
.edu-heading {
  font-size: 32px;
  font-weight: 700;
  color: #138781;
  /* letter-spacing: 2px; */
 
}

.edu-hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
  margin-bottom: 20px;
}

/* CARD */
.edu-card {
  /* width: 900px; */
  margin: auto;
  background: #ffffff;
  padding: 28px 160px 28px 20px;
  border-radius: 16px;
  display: flex;
  gap: 22px;
  align-items: center;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
 
}

/* ICON */
.edu-icon {
  width: 56px;
  height: 56px;
  background: #138781;
  color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
  flex-shrink: 0;
}

/* TEXT */
.edu-year {
  font-size: 14px;
  color: #138781;
  font-weight: 500;
}

.edu-details h3 {
  margin: 6px 0;
  font-size: 22px;
  color: #1f2937;
}

.edu-details p {
  margin: 0;
  color: #4b5563;
  font-size: 15px;
}

/* ============ DARK MODE ============ */

body.dark .edu-heading {
  color: #38bdf8;
}

body.dark .edu-hr {
  border-color: #38bdf8;
}

body.dark .edu-card {
  background: #1e293b;
  box-shadow: 0 14px 35px rgba(0, 0, 0, 0.55);
}

body.dark .edu-icon {
  background: linear-gradient(135deg, #38bdf8, #0ea5e9);
  color: #020617;
}

body.dark .edu-year {
  color: #38bdf8;
}

body.dark .edu-details h3 {
  color: #f8fafc;
}

body.dark .edu-details p {
  color: #94a3b8;
}

/* RESPONSIVE */
@media (max-width: 600px) {
  .education-wrapper {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    width: 100%;
  }
  .education-wrapper h2 {
    margin-top: 10px;
  }
  .education-wrapper .edu-hr{
     width: 280px;
  }
}
