/* =========================
   HOME SECTION
========================= */
.home-container {
  min-height: 300px;
  padding-left: 150px;
  padding-top: 60px;

  color: #020617; /* dark text, bg colorful */
  display: flex;
  align-items: center;
}

.home-content {
  animation: fadeUp 0.9s ease;
}

/* =========================
   HEADINGS
========================= */
.home-content h2 {
  font-size: 26px;
  font-weight: 500;
  margin-bottom: 6px;
}

.home-content h1 {
  font-size: 42px;
  font-weight: 800;
  margin: 8px 0 12px;

  background: linear-gradient(
    90deg,
    #6366f1,
    #a855f7,
    #ec4899
  );

  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

/* =========================
   TYPEWRITER TEXT
========================= */
.typewriter-text {
  color: #ef4444;
  font-weight: 600;
}

/* =========================
   BUTTONS
========================= */
.home-buttons {
  margin-top: 26px;
  display: flex;
  gap: 16px;
}

.home-buttons .btn-hire,
.home-buttons .btn-cv {
  border: none;
  padding: 12px 26px;
  font-size: 15px;
  font-weight: 600;
  border-radius: 999px;
  cursor: pointer;
  transition: all 0.4s ease;
}

/* Hire Me */
.home-buttons .btn-hire {
  background: linear-gradient(
    135deg,
    #3b82f6,
    #6366f1
  );
  color: white;
  box-shadow: 0 12px 30px rgba(59, 130, 246, 0.35);
}

.home-buttons .btn-hire:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 20px 50px rgba(99, 102, 241, 0.5);
}

/* Download CV */
.home-buttons .btn-cv {
  background: linear-gradient(
    135deg,
    #f97316,
    #ec4899
  );
  color: white;
  box-shadow: 0 12px 30px rgba(236, 72, 153, 0.35);
}

.home-buttons .btn-cv:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 20px 50px rgba(236, 72, 153, 0.5);
}

/* =========================
   THEME BUTTON
========================= */
.theme-btn {
  position: fixed;
  right: 0;
  top: 5%;

  height: 50px;
  width: 50px;

  background: linear-gradient(
    135deg,
    #f02d1f,
    #60f133
  );

  color: white;
  display: flex;
  align-items: center;
  justify-content: center;

  cursor: pointer;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  box-shadow: -8px 10px 25px rgba(0, 0, 0, 0.35);
  z-index: 5;
}

/* =========================
   ANIMATION
========================= */
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* =========================
   MOBILE VIEW
========================= */
@media (max-width: 600px) {
  .home-container {
    padding: 0 18px;
    padding-top: 60px;
    min-height: auto;
  }

  .home-content h1 {
    font-size: 28px;
  }

  .home-content h2 {
    font-size: 20px;
  }

  .home-buttons {
    flex-direction: column;
    gap: 14px;
    padding-bottom: 17px;
  }

  .home-buttons .btn-hire,
  .home-buttons .btn-cv {
    width: 100%;
    text-align: center;
  }
}
